<template>
  <div>
    <div v-for="item in list"
         :key="item.art_id.toString()">
      <van-cell :to="`/article/${item.art_id}`"
                class="collect-item">
        <div slot="title"
             class="title van-multi-ellipsis--l2">
          {{item.title}}
        </div>
        <div slot="label">
          <div class="text">
            <span>{{item.aut_name}}</span>
            <span>{{item.pubdate|format}}</span>
          </div>
        </div>
        <van-image slot="default"
                   v-if="item.cover.type===1"
                   fit="cover"
                   src="" />
        <div slot="label"
             v-else-if="item.cover.type===3"
             class="img-three">
          <van-image :src="item.cover.images[0]"
                     fit="cover" />
          <van-image :src="item.cover.images[1]"
                     fit="cover" />
          <van-image :src="item.cover.images[2]"
                     fit="cover" />
        </div>
      </van-cell>
      <div slot="label"
           class="btn">
        <van-button icon="comment-o">评论</van-button>
        <van-button icon="good-job-o">点赞</van-button>
        <van-button icon="star-o">收藏</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { getUserCollect } from '@/api/user'
export default {
  data () {
    return {
      list: []
    }
  },
  filters: {
    format (value) {
      return dayjs(value).format('YYYY-MM-DD')
    }
  },
  methods: {
    async loadUserCollect () {
      const { data: res } = await getUserCollect({
        page: 1,
        per_page: 10
      })
      console.log(res)
      this.list = res.data.results
    }
  },
  created () {
    this.loadUserCollect()
  }
}
</script>

<style lang="scss" scoped>
.collect-item {
  margin-top: 10px;
  padding-bottom: 0;
  .title {
    font-size: 32px;
    color: #3a3a3a;
  }
  .text {
    font-size: 23px;
    color: #b4b4b4;
    span {
      margin-right: 27px;
    }
  }
  .van-image {
    width: 232px;
    height: 146px;
  }
  .van-cell__value {
    flex: unset;
    width: 232px;
    height: 146px;
    padding-left: 25px;
  }
  .img-three {
    display: flex;
    justify-content: space-between;
    margin: 30px 0;
    .van-image {
      flex: 1;
      &:not(:last-child) {
        padding-right: 4px;
      }
    }
  }
}
.btn {
  display: flex;
  .van-button {
    flex: 1;
  }
}
</style>
